@import '../../../themes/ikiTheme.scss';

.button {
    text-transform: capitalize !important;
    font-family: "Open Sans", sans-serif !important;
    //font-weight: $font-light !important;
    font-weight: 400 !important;
    font-size: $font-size-input;
    display: inline-flex;
    align-items: center;
    box-shadow: none !important;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    &.left {
      text-align: left;
      justify-content: flex-start;

      svg {
        font-size: 20px;
        margin-right: 8px;
      }
    }

    &.warning {
      background-color: #ff9800 !important;
      color: #fff !important;
      //background-color: #ffc107 !important;
      //color: #555 !important;
    }

    :global(.zmdi) {
      margin-right: 7px;
    }

    .btnSpinner {
      font-size: 10px;
      /*margin: 50px auto;*/
      margin-right: 11px;
      text-indent: -9999em;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background: #e8e8e8;
      background: -moz-linear-gradient(
        left,
        #e8e8e8 10%,
        rgba(232, 232, 232, 0) 42%
      );
      background: -webkit-linear-gradient(
        left,
        #e8e8e8 10%,
        rgba(232, 232, 232, 0) 42%
      );
      background: -o-linear-gradient(left, #e8e8e8 10%, rgba(232, 232, 232, 0) 42%);
      background: -ms-linear-gradient(
        left,
        #e8e8e8 10%,
        rgba(232, 232, 232, 0) 42%
      );
      background: linear-gradient(
        to right,
        #e8e8e8 10%,
        rgba(232, 232, 232, 0) 42%
      );
      position: relative;
      -webkit-animation: load3 1.4s infinite linear;
      animation: load3 1.4s infinite linear;
      -webkit-transform: translateZ(0);
      -ms-transform: translateZ(0);
      transform: translateZ(0);

      &:before {
        width: 50%;
        height: 50%;
        background: #e8e8e8;
        border-radius: 100% 0 0 0;
        position: absolute;
        top: 0;
        left: 0;
        content: '';
      }

      &:after {
        background: #3f51b5;
        width: 75%;
        height: 75%;
        border-radius: 50%;
        content: '';
        margin: auto;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
      }

      &.alternate {
        &:before {
          background: #3f51b5;
        }
      }
      &.alternate {
        &:after {
          background: #e8e8e8;
        }
      }

      &.on-error {
        &:before {
          background: #e8e8e8;
        }
      }
      &.on-error {
        &:after {
          background: #f44336;
        }
      }
    }
}

  // Button animation - Spinner
  @-webkit-keyframes load3 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes load3 {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  