.visualize-dialog .MuiDialog-paper {
  width: 90%;
  height: 90%;
}
.visualize-dialog iframe {
  border: none !important;
  border-top: 2px solid rgba(0, 0, 0, 0.54) !important;
}
.visualize-dialog .title {
  padding: 0;
  background: #f9fbfc;
}
.visualize-dialog .title .close-btn {
  float: right;
}

.visualize-dialog .iframe-container {
  height: calc(100% - 48px);
  .iframe-cover {
    height: 70px;
    position: absolute;
    width: 100%;
    background-color: #f9fbfc;
  }
}

.browse-status {
  font-size: 16px;
}
