.peekWrap {
    height: 350px;
    position: fixed;
    bottom: -350px;
    left: 310px;
    right: 240px;
    padding: 28px 50px;
    background-color: #FFF;
    border-top: 1px solid rgba(0, 0, 0, 0.04);
    z-index: 2;
    transition: all 1s ease !important;

    :global {
        .peek-wrap-header {
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .peek-wrap-title {
                display: inline-block;
                font-size: 22px;
                font-weight: bold;
                flex: 1 1;
                color: #555555;
            }
        }

        .peek-wrap-content {
            position: relative;
            margin-top: 16px;
            overflow: auto;
            margin-bottom: 50px;
            height: 250px;
            transition: height 1s ease !important;
    
            .iki-loader-wrap {
                &.peek-load-wrap {
                    position: absolute;
                    background-color: #FFF;
                }
            }
        }
    }

    &.show {
        bottom: 0;
        transition: all 1s ease !important;

        &.max {
            height: 100%;
            transition: all 1s ease !important;

            :global(.peek-wrap-content) {
                height: 93%;
                transition: height 1s ease !important;
            }
        }
    }
}