ul.bottom-menu {
  //height: 100%;
  margin: 0;
  margin-top: auto;
  margin-bottom: 50px;
  padding: 0;

  li.menu {
    width: 100%;
    margin-bottom: 10px;
    list-style-type: none;

    .link-alike {
        height: 36px;
        display: flex;
        align-items: center;
        margin: 0 13px;
        padding-left: 7px;
        padding-top: 6px;
        padding-bottom: 6px;
        background-color: transparent !important;
        border-radius: 5px;
        opacity: 0.75;
        text-decoration: none;
        text-transform: capitalize;
        cursor: pointer;

        &:hover {
            background: transparent !important;
            background-color: transparent !important;
            opacity: 1;
        }
    }

    i {
        color: #ffffff;
        font-size: 22px;
        margin-right: 12px;
    }

    &.open {
        width: 100%;
        a {
            background-color: rgba(255, 255, 255, 0.16) !important;
            opacity: 1;
            padding-top: 6px;
            padding-bottom: 6px;
        }
    }

    &.profile-menu-item {
        position: absolute;
        bottom: 5px;
    }
    
    span.nav-text {
        color: #ffffff;
        font-family: 'Open Sans', sans-serif;
        font-size: 16px;
    }
  }
}