  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  80% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  80% {
    opacity: 1; }
  100% {
