.MuiTabs-root {
    border-bottom: 1px solid rgba(85,85,85,.14);

    .MuiButtonBase-root.MuiTab-root {
        min-width: auto;
        font-family: Open Sans,sans-serif;
        font-size: 14px;
        font-weight: 600 !important;
        opacity: 0.5;
        text-transform: capitalize;
        color: #3f51b5;

        &.Mui-selected {
            opacity: 1;
            color: #3f51b5;
        }
    }

    .flow-tab-indicator {
        background-color: #3f51b5;
    }
}