@import '../../themes/ikiTheme.scss';

.dialog {
    color: $text-color;

        .header {
            //padding: 16px 0 32px 0;
            padding: 32px 32px 0 32px;
            color: $text-color;

            .title {
                padding: 0;
                font-size: 24px;
                font-weight: 700;
                text-transform: capitalize;
                font-family: 'Open Sans', sans-serif;
            }

            .subtitle {
                font-size: 12px;
                opacity: .5;
                font-weight: 500;
                text-transform: capitalize;
                font-family: 'Open Sans', sans-serif;
            }
        }

        .content {
            margin: 16px 0 24px 0;
            padding: 16px 32px;
            line-height: $line-height-m;
            //overflow-y: auto;

            :global(.section:not(:last-child)) {
                margin-bottom: 32px;
            }

            :global(.section:last-child) {
                margin-bottom: 8px;
            }
        }

        .footer {
            padding: 0 32px 32px 32px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            //padding: 32px;

            & > button:not(:last-child) {
                margin-right: 16px;
            }
        }

    &.center {
        text-align: center;
        .footer {
            text-align: center;
            justify-content: center;
        }
    }
}

.dialog {
    &.delete {
        :global(.MuiButton-containedPrimary) {
            background-color: $error !important;
        }
        .header {
            .title {
                color: $error;
            }
        }
    }
}