.rootCanvas {
    height: 100vh;
}

.dropArea {
  height: 100vh;
  left: 310px;
  right: 240px;
  position: absolute;
  border-left: 1px solid rgba(0, 0, 0, 0.04);
  transition: all 1s ease;

  :global {
    .drop-area-bg {
      position: absolute;
      width: 100%;
      height: 100%;
      background-size: 25px 25px;
      background-color: #fff;
      //background-color: rgba(63, 81, 181, 0.04);
      background-image: repeating-linear-gradient(0deg, #f4f4f4, #f4f4f4 1px, transparent 1px, transparent 25px), repeating-linear-gradient(-90deg, #f4f4f4, #f4f4f4 1px, transparent 1px, transparent 25px);
    }

    .buttons,
    .undo-buttons {
      position: absolute;
      z-index: 1;
      bottom: 0;
      right: 0;
      margin-bottom: 50px;
      margin-right: 50px;
      display: flex;
      flex-direction: column;
      background: rgba(85, 85, 85, 0.09);
      border-radius: 9px;

      .MuiIconButton-root {
        padding: 12px !important;
        border-radius: 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.04);
      }

      .MuiIconButton-root:first-child {
        border-top-right-radius: 9px;
        border-top-left-radius: 9px;
      }

      .MuiIconButton-root:last-child {
        border-bottom: 0;
        border-bottom-right-radius: 9px;
        border-bottom-left-radius: 9px;
      }
    }

    .undo-buttons {
      left: 0;
      right: unset !important;
      margin-left: 50px;
      margin-right: 0;
      right: inherit;
      flex-direction: row;
    }
  }
}

.interactiveFrame :global {
  .interactive-frame-tabs{
    position: fixed;
    top: 0;
    left: 310px;
    right: 240px;
    //padding: 30px 50px;
    margin: 30px 50px;
    padding: 8px 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #FFF;
    border-radius: 5px;

    .tabs-title {
      .title{
        margin-right: 20px;
        font-size: 16px;
        font-weight: bold;
        flex: 1 1;
        color: #555555 !important;
      }
    }

    .interactive-frame-tab{
      display: inline-flex;
      align-items: center;
      margin-right: 6px;
      padding: 3px 6px 3px 13px;
      border-radius: 5px;
      background-color: rgba(0,0,0,0.03);
      //background-color: transparent;
      //background-color: #FFF;
      //background-color: rgba(0, 0, 0, .05);
      //background-color: rgba(85, 85, 85, 0.09);

      .inner-tab-div {
        opacity: 0.8;
        text-transform: capitalize;

        &:hover {
          opacity: 1;
          cursor: pointer;
        }

        i {
          margin-right: 4px;
        }
      }

      .close-interactive-tab{
        margin-left: 2px;
        font-size: 8px !important;

        .close-interactive-tab-icon{
          width: 16px;
          height: auto;
        }
      }
    }
  }
}