@import '../../../themes/ikiTheme.scss';

.drawerHolder :global {
    .drawer {
        width: 250px;
        background-color: #3f51b5 !important;
        overflow: visible !important;
        transition: width 0.2s ease;

        &.minimized {
            width: 60px;
        }

        .pureBg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            //background-color: #3f51b5 !important;
            background-color: #FBFCFD;
            z-index: -1;
        }
    }
    /*.drawer-back-icon {
        transform: rotate(180deg);
    }*/
}

.drawerHolder {
    .goBack {
        position: absolute;
        top: 70px;
        padding: 0 17px;
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #FFF;
        opacity: 0.75;

        &:hover {
            opacity: 1;
        }

        .goBackIcon {
            margin-right: 8px;
            transform: rotate(180deg);
        }
    }

    &.minimized {
        .toggleNavButton {
            padding-left: 0;
        }
        
        :global(.drawer) {
            width: 60px;
            transition: width 0.2s ease;
        }

        .goBack {
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            color: #FFF;
            opacity: 0.75;
            position: absolute;
            top: 70px;
            width: 100%;
    
            &:hover {
                opacity: 1;
            }
    
            .goBackIcon {
                margin-right: 0;
                transform: rotate(180deg);
            }
        }
    }
}

