// right sidebar

.infoDrawerHolder {
    width: 240px;
    flex-shrink: 0;
    background-color: #FFF;

    :global {
        .info-drawer-paper {
            width: 240px;
            padding: 20px;

            &.MuiDrawer-paperAnchorDockedRight {
                border-left: 1px solid rgba(0, 0, 0, 0.04);
            }

            .info-drawer-root {
                .info-drawer-section {
                    padding: 7px 0;

                    .info-drawer-title {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        .title {
                            font-size: 22px;
                            font-weight: 700;
                            color: #555 !important;
                        }
                    }
                    
                    .info-drawer-title-group {
                        font-size: 12px;
                        margin-top: 20px !important;
                    
                        .info-drawer-title-title {
                            color: #b2b0b0;
                            text-transform: capitalize;
                        }
                    
                        .info-drawer-title-subtitle {
                            margin-top: 4px;
                            color: #555;
                            font-weight: 600;
                            text-transform: capitalize;
                        }
                    }

                    .info-drawer-button-group {
                        button {
                            margin-top: 15px;
                        }
                    }

                    &.status-section-dataset {
                        .status-holder {
                            margin-top: 16px;
                            font-size: 14px;
                            color: #555;
                            /*max-height: calc(100% - 215px);*/
                            /*min-height: 210px;*/
                            margin-right: 0;
                            padding-right: 10px;
                            /*height: 180px;*/
                            /*height: 100%;*/
                            height: calc(100% - 40px);
                            overflow-y: auto;
                            scrollbar-width: thin;
                            scrollbar-color: rgba(220, 220, 235, 0.8) rgba(230, 230, 245, 0.4);

                            &::-webkit-scrollbar {
                                width: 7px;
                                height: 7px;
                            }
                            &::-webkit-scrollbar-track-piece {
                                background-color: rgba(230, 230, 245, 0.4);
                            }
                            &::-webkit-scrollbar-thumb:vertical {
                                height: 30px;
                                background-color: rgba(220, 220, 235, 0.8);
                            }

                            .status-row {
                                display: flex;
                                align-items: center;
                                margin-bottom: 10px;

                                &.success {
                                    color: #4caf50;
                                }

                                &.failed {
                                    color: #f44336;
                                }

                                .status-icon {
                                    margin-right: 7px;
                                }

                                .status-data {
                                    width: calc(100% - 20px);

                                    .status-timestamp {
                                        float: none;
                                    }

                                    .status-user {
                                        float: none;
                                    }

                                    .status-run_type {
                                        display: none;
                                    }

                                    .status-message {
                                        display: none;
                                    }
                                }
                            }
                        }
                    }

                    &.status-section {
                        position: fixed;
                        bottom: 20px;
                        right: 0;
                        width: 239px;
                        height: calc(100% - 350px);
                        padding: 19px;
                        background-color: #fff;
                        border-top: 1px solid transparent;
                        //border-left: 1px solid transparent;
                        border-left: 1px solid rgba(0, 0, 0, 0.04) !important;
                        border-top-left-radius: 5px;
                        /*box-shadow: 0 0 0 transparent;*/
                        transition: all 0.15s linear !important;
                        -webkit-transition: all 0.2 cubic-bezier(0.68, -0.55, 0.265, 1.55) !important;

                        .status-holder {
                            margin-top: 16px;
                            font-size: 14px;
                            color: #555;
                            /*max-height: calc(100% - 215px);*/
                            /*min-height: 210px;*/
                            margin-right: 0;
                            padding-right: 10px;
                            /*height: 180px;*/
                            /*height: 100%;*/
                            height: calc(100% - 40px);
                            overflow-y: auto;
                            scrollbar-width: thin;
                            scrollbar-color: rgba(220, 220, 235, 0.8) rgba(230, 230, 245, 0.4);

                            &::-webkit-scrollbar {
                                width: 7px;
                                height: 7px;
                            }
                            &::-webkit-scrollbar-track-piece {
                                background-color: rgba(230, 230, 245, 0.4);
                            }
                            &::-webkit-scrollbar-thumb:vertical {
                                height: 30px;
                                background-color: rgba(220, 220, 235, 0.8);
                            }

                            .status-row {
                                display: flex;
                                align-items: center;
                                margin-bottom: 10px;

                                &.success {
                                    color: #4caf50;
                                }

                                &.failed {
                                    color: #f44336;
                                }

                                .status-icon {
                                    margin-right: 7px;
                                }

                                .status-data {
                                    width: calc(100% - 20px);

                                    .status-timestamp {
                                        float: none;
                                    }

                                    .status-user {
                                        float: none;
                                    }

                                    .status-run_type {
                                        display: none;
                                    }

                                    .status-message {
                                        display: none;
                                    }
                                }
                            }
                        }

                        &.is-open {
                            width: 600px;
                            height: calc(100% - 280px);
                            transition: all .15s linear !important;
                            border-top: 1px solid rgba(0,0,0,.13);
                            border-left: 1px solid rgba(0,0,0,.13);
                            border-bottom: 1px solid rgba(0,0,0,.13);

                            .status-holder {
                                .status-row {
                                    align-items: flex-start !important;

                                    .clip-txt {
                                        white-space: normal;
                                        overflow: auto;
                                        text-overflow: unset;
                                    }

                                    .status-message {
                                        display: block;
                                        clear: both;

                                        &.clip-txt {
                                            width: 95%;
                                            overflow-wrap: break-word;
                                            white-space: pre-wrap;
                                        }
                                    }

                                    .status-timestamp {
                                        float: left;
                                    }

                                    .status-user {
                                        float: left;
                                        margin-left: 20px;
                                    }

                                    .status-run_type {
                                        display: block;
                                        float: left;
                                        margin-left: 20px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.logoutBtn {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    padding-left: 20px;
    color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    background-color: transparent;
    opacity: 0.75;

    i {
        font-size: 22px;
        margin-right: 12px;
    }
    &:hover {
        opacity: 1;
        cursor: pointer;
    }
  }

  .toggleNavButton {
    position: absolute;
    bottom: 16px;
    right: -18px;
    background-color: rgb(235, 235, 235);
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 32px;
    //border: 1px solid rgb(235, 235, 235);
    padding-left: 4px;

    svg {
        font-size: 16px;
        color: $primary;
    }

    &:hover {
        cursor: pointer;
        background-color: rgb(230, 230, 230);
    }
}