.rbc-calendar-holder {
    height: 100%;

    .rbc-calendar {
        min-height: 400px;
        height: 100%;

        .rbc-toolbar {
            margin-bottom: 24px;
            
            button {
                padding: 8.5px 16px;
                background-color: #fff;
                box-shadow: 0 0 0 transparent;
                color: #b2b0b0;
                font-size: 14px;
                font-family: 'Open Sans', sans-serif;

                &:hover {
                    color: #555;
                    cursor: pointer;
                }

                &:active,
                &.rbc-active {
                    background-color: rgba(0, 0, 0, 0.09);
                    color: #555;
                }
            }
        }

        .rbc-month-view {
            border-radius: 5px;
            border-color: rgba(0, 0, 0, 0.04);

            .rbc-month-header {
                .rbc-header {
                    padding: 8px;
                    border-color: rgba(0, 0, 0, 0.04);
                    color: #555;
                    font-family: 'Open Sans', sans-serif;
                    font-size: 14px;
                    font-weight: bold;
                }
            }

            .rbc-row {
                .rbc-date-cell {
                    &.rbc-off-range {
                        color: #bebebe;
                    }
                }
            }

            .rbc-month-row {
                .rbc-date-cell {
                    padding: 8px;
                    text-align: left;

                    a {
                        margin: 8px;
                        font-size: 18px;
                        font-weight: 600;
                    }
                }

                .rbc-row-bg {
                    .rbc-day-bg + .rbc-day-bg {
                        border-color: rgba(0, 0, 0, 0.04);
                    }
                }
            }

            .rbc-month-row + .rbc-month-row {
                border-color: rgba(0, 0, 0, 0.04);
            }

            .rbc-day-bg {
                &.rbc-off-range-bg {
                    background: rgba(0, 0, 0, 0.02);
                }
            }

            .rbc-row-segment {
                padding: 4px 8px;

                .rbc-event {
                    background-color: rgba(63, 81, 181, 0.6);
                    //background-color: rgba(63, 81, 181, 1);
                    //background-color: rgba(0, 0, 0, 0.34);
                    //color: rgb(63, 81, 181);
                    //color: rgba(0, 0, 0, 0.54);
                    color: #fff;
                    font-family: 'Open Sans', sans-serif;
                    font-weight: 600;

                    &:hover {
                        //background-color: rgba(0, 0, 0, 0.54);
                        background-color: rgba(63, 81, 181, 0.9);
                        //background-color: rgba(63, 81, 181, 0.9) !important;
                    }
                }

                .rbc-show-more {
                    color: rgba(63, 81, 181, 1);
                }
            }
        }

        .rbc-time-view {
            border-radius: 5px;
            //border-color: rgba(0, 0, 0, 0.04);
        }

        .rbc-day-slot {
            .rbc-event {
                border: none !important;
            }
        }

        .rbc-agenda-table {
            tbody {
                tr {
                    background-color: transparent !important;
                }
            }
        }
    }
}