.customNode {
    .buttonGroup {
        width: 100%;
        display: flex;
        margin-top: -20px;
        padding-bottom: 15px;
        pointer-events: none;
        justify-content: space-between;
        opacity: 0;
        transition: opacity 0.3s ease !important;
    }

    &:hover {
        .buttonGroup {
            opacity: 1;
            pointer-events: all;
            transition: opacity 0.3s ease !important;
        }
    }

    .customNodeCard {
        position: relative;
        padding: 30px;
        overflow: initial !important;
        width: 150px;
        transition: all 1s ease !important;
        background: #ffffff;
        box-shadow: 0 0 0 transparent !important;
        border: 3px solid rgba(63, 81, 181, 0.15);

        .circlePort {
            transition: all 1s ease !important;
            width: 14px;
            height: 14px;
            /*margin: 2px;*/
            border-radius: 50%;
            background: rgb(63, 81, 181);
          
            cursor: pointer;
        }
        
        &.error {
          background: #ffcdd2;
          border: 3px solid #f44336;

          .customNodeName {
            color: #f44336;
          }

          .facetNameHolder {
            .facetName {
                background: #ff1744;
                color: #fff;
            }
          }

          .circlePort {
            background: #f44336;
          }
        }

        .facetNameHolder {
            position: absolute;
            top: -12px;
            left: 0;
            width: 100%;

            .facetName {
                width: 85%;
                margin: 0 auto;
                padding: 2px 6px 3px;
                color: #4b5cba;
                text-align: center;
                font-size: 12px;
                text-transform: capitalize;
                background-color: #ebedf7;
                border-radius: 4px;
            }
        }

        :global {
            .port {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);

                &.in {
                    left: -8px;
                }

                &.out {
                    right: -8px;
                }
            }
        }

        .customNodeName {
            font-weight: 700;
            font-size: 16px;
            color: #3f51b5;
            text-align: left;
            transition: all 1s ease !important;

            .customNodeHeader {
                font-size: 12px;
                opacity: .5;

                .customNodeChainGroup {
                    font-size: 10px;
                    font-weight: 400;
                }
            }

            .customNodeTitle {
                margin-top: 20px;
            }
        }
    }
}

.errorList {
    padding-left: 16px;
}

.errorMessage {
    font-size: 14px;
    margin-left: 0;
}

.errorMessageTitle {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 8px;
    padding-bottom: 8px;
}

.nodeErrorIcon {
    color: #ff1744;
    //color: #f44336;
    margin-left: auto;
    position: absolute;
    top: 38px;
    right: -25px;
    cursor: pointer;
}