.help-drawer-wrap{  
    position: fixed;
    top: 0;
    left: 0;
    width: 248px;
    height: 100%;
    padding: 24px;
    background-color: transparent;
    //border-right: 1px solid rgba(0, 0, 0, 0.04);
    z-index: -1;
    transition: all 0.2s ease;
    &.is-open {
        left: 310px;
        width: 400px;
        background-color: #FFF;
        transition: all 0.2s ease;
    }
    .help-drawer-inner {
        height: 100%;
    }
    .info-example-block {
        //overflow: scroll;
       //height: 90%;
       img {
           width: 100%;
           border: 1px solid rgba(85, 85, 85, 0.14);
       }
    }
    .info-block{
        //max-height: 600px;
        height: calc(100% - 100px);
        overflow-y: auto;
        h1, h2, h3{
            color: #555 !important;
            font-family: "Open Sans", sans-serif;
            font-weight: 600;
        }
        h1{
            font-size: 24px;
        }
        h2{
            font-size: 18px;
        }
        h3{
            font-size: 16px;
        }
        blockquote {
            margin: 0;
            padding: 10px 0 10px 0;
            color: #555;
            ol, ul{
                padding-left: 32px;
                //color: rgba(0, 0, 0, 0.54);
                color: #555;
            }
        }
        p {
            margin-top: 4px;
            //color: rgba(0, 0, 0, 0.54);
            color: #555;
        }
    }
    .help-tabs {
        margin-bottom: 24px;
        color: #3f51b5 !important;
        border-bottom: 1px solid rgba(85, 85, 85, 0.14);
        .Mui-selected .MuiTouchRipple-root{
            border-bottom: 3px solid;
        }
        button {
            min-width: auto;
            font-weight: bold;
        }
    }
    .close-help-drawer-btn {
        position: absolute;
        top: 10px;
        right: 13px;
        z-index: 1;
    }
}