
.contentArea {
    margin-left: 250px;
    padding-top: 0;
    position: absolute;
    top: 0;
    width: calc(100% - 260px);
    height: 100%;
    background-color: #FBFCFD;
    transition: width 0s ease, margin-left 0.2s ease;

    &.minimized {
        margin-left: 60px;
        width: calc(100% - 60px);
        transition: width 0s ease, margin-left 0.2s ease 0.2s;
    }

    :global(.main-container) {
        padding: 26px 32px !important;
    }

    :global(.h_100-100) {
        height: calc(100% - 100px);
    }

    :global(.h-100) {
        height: 100%;
    }

    :global(.central-panel){
        position: absolute;
        top: -88px;
        left: 250px;
        right: 240px;
        height: calc(100% + 88px);
        border: 0;
        z-index: 1;

        &::before{
            content: "";
            position: absolute;
            width: 100%;
            //height: 135px;
            height: 80px;
            background-color: #FFF;
            z-index: 2;
        }

        &.dataset{
            top: -58px;
            height: calc(100% + 58px);
        }
    }

    :global(.metabase-iframe){
        width: 100%;
        height: 100%;
        border: 0;
    }
}