.textDivider {
  position: relative;
  display: block;
  width: 100%;
  text-align: center;
  margin: 16px 0;
  
  &::after {
    content: "";
    position: absolute;
    top: 49%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(0,0,0,.13);
    z-index: 1;
  }

  span {
    display: inline-block;
    color: #555;
    background-color: #fff;
    padding: 0 16px;
    position: relative;
    z-index: 2;
  }
}