.itemBox {
    justify-self: stretch;
    padding: 13px 29px;
    border: 3px solid rgba(63,81,181,.1);
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 0 0 transparent !important;
    overflow: hidden;
    box-sizing: border-box;
    position: relative;

    &:hover {
        border: 3px solid rgba(63, 81, 181, 0.25);
    }
    &.uploading {
        padding-bottom: 20px;

        .uploadingDatabox {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(235, 235, 235, 0.4);
            z-index: 99;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            padding: 0 24px;
            padding-bottom: 8px;

            div {
                width: 100%;
                font-size: 12px;

                /*.linearProgress {
                    font-size: 12px;
                }*/
            }
        }
    }
    &.uploading:hover {
        border: 3px solid rgba(63, 81, 181, 0.10);
    }
    &.clicked {
        border: 3px solid rgba(63, 81, 181, 0.4) !important;
    }

    .header {
        display: flex;
        align-items: center;

        .icon :global {
            svg {
                font-size: 20px;
                margin-top: 2px;
                color: #b2b0b0;
                height: 15px;
                width: auto;
            }
        }

        .type {
            margin-left: 8px;
            color: #b2b0b0;
            text-transform: uppercase;
            letter-spacing: .1em;
            font-family: Open Sans,sans-serif;
            font-size: 10px !important;
            font-weight: 400;
        }

        .menu {
            margin-left: auto;
        }
    }

    .content {
        padding: 16px 0 24px 0;
        
        .link {
            color: #555555;
            outline: 0;
            cursor: pointer;

            & > div {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        & > * {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            line-height: 21px;
        }

        .title {
            color: #3f51b5 !important;
            font-family: Open Sans,sans-serif;
            font-size: 16px !important;
            font-weight: 700 !important;
            letter-spacing: 0 !important;
            text-transform: capitalize;
            margin-bottom: 2px;
        }

        .faded1 {
            font-size: 12px !important;
            font-weight: 400 !important;
            color: #b2b0b0 !important;
            letter-spacing: 0 !important;
            font-style: normal !important;
        }

        .newSection {
            margin-top: 20px;
        }
    }
}