.tableHolder {
    //background-color: transparent !important;
    border: none;
    box-shadow: 0 0 0 transparent !important;
    padding: 0 !important;
    margin-top: -16px;

    .MuiCardContent-root,
    .table-inside {
        padding: 0 !important;
    }
}
  
.table {
    width: 100%;
    margin-bottom: 0;

    .MuiTableCell-head {
        padding: 16px 8px;
        padding-top: 8px;
        padding-bottom: 8px;
        color: #555;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;
        font-weight: bold;
        line-height: 1.33;
        letter-spacing: 0em;
        //border-bottom: 3px solid rgba(63, 81, 181, 0.1);
        border-bottom: 2px solid rgba(0, 0, 0, 0.1);

        &.MuiTableCell-stickyHeader {
            background-color: #FFF !important;
        }

        .MuiTableSortLabel-root {
            font-family: 'Open Sans', sans-serif;
            font-weight: 600 !important;
            &.MuiTableSortLabel-active {
                color: #3F51B5;
                & svg {
                    color: #3F51B5;
                }
            }
            & svg{
                color: #3F51B5;
                opacity: 0.14;
            }
            &:hover {
                color: #3F51B5;
            }
        }  
    }

    .MuiTableRow-root {
        background-color: transparent;
        &:hover{
            background-color: rgba(241, 242, 244, 0.65);
        }
        .clicked{
            background-color: rgba(241, 242, 244, 0.85);
        }
    }

    .MuiTableHead-root {
        .MuiTableRow-root {
            background-color: transparent;
            &:last-child {
                //border-bottom: 3px solid rgba(63, 81, 181, 0.1);
                border-bottom: 2px solid rgba(0, 0, 0, 0.1);
            }
        }
    }

    .MuiTableCell-body {
        padding: 4px 8px;
        color: #555555;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;
        font-weight: normal;
        line-height: 1.33;
        letter-spacing: 0em;
        border-bottom: 1px solid rgba(63, 81, 181, 0.1);
        cursor: default !important;
        //force one line cell content
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        &.first-row-col {
            color: #b2b0b0;
        }

        a {
            color: #555555;
            &:hover {
                color: #3F51B5;
                cursor: pointer;
            }
        }
        .project-id,
        .project-date {
            width: 175px;
        }
        .project-buttons {
            width: 120px;
        }
        &:last-child {
            .MuiIconButton-root {
                padding: 6px !important;
            }
        }
    }
}

.MuiTablePagination-root {
    color: rgba(0, 0, 0, 0.54) !important;
    //color: #b2b0b0 !important;
}