.interactiveWrap {
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: -1;
    transition: opacity .5s ease,z-index .5s step-end !important;

    :global {
        .interactive-wrap-drawer {
            position: absolute;
            top: 0;
            left: 0;
            width: 250px;
            height: 100%;
            display: flex;
            flex-direction: column;
            color: rgba(255, 255, 255, 0.8) !important;
            border-right: 0 none !important;
            z-index: 1250 !important;
            background-color: #252525 !important;
            opacity: 0;
            transition: opacity 0.5s ease !important;

            .back-btn-holder {
                width: 100%;
                padding: 0 13px 40px;
                margin-top: 16px;

                .viz-drawer-btn {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    height: auto;
                    cursor: pointer;
                    padding-left: 7px;
                    margin-bottom: 10px;
                    font-size: 16px;
                    opacity: .75;

                    &:hover {
                        opacity: 1;
                    }
    
                    i.zmdi {
                        margin-right: 6px;
                    }
                }
            }
        }

        .interactive-wrap-rightbar {
            position: absolute;
            top: 0;
            right: 0;
            width: 240px;
            height: 100%;
            display: flex;
            flex-direction: column;
            /*color: rgba(255, 255, 255, 0.8) !important;*/
            border-right: 0 none !important;
            border-left: 1px solid rgba(0, 0, 0, 0.04);
            z-index: 1250 !important;
            background-color: #fff !important;
            opacity: 0;
            transition: opacity 0.5s ease !important;

            .info-drawer-root {
                padding: 20px;

                .info-drawer-section {
                    padding: 7px 0;

                    .info-drawer-title {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        .title {
                            font-size: 22px;
                            font-weight: 700;
                            color: #555 !important;
                        }
                    }
                    
                    .info-drawer-title-group {
                        font-size: 12px;
                        margin-top: 20px !important;
                    
                        .info-drawer-title-title {
                            color: #b2b0b0;
                            text-transform: capitalize;
                        }
                    
                        .info-drawer-title-subtitle {
                            margin-top: 4px;
                            color: #555;
                            font-weight: 600;
                            text-transform: capitalize;
                        }
                    }
                }
            }   
        }

        .interactive-central-holder{
            position: absolute;
            top: 0;
            left: 250px;
            right: 240px;
            height: 100vh;
            //top: -20px;
            //height: calc(100% + 20px);
            background-color: rgba(255, 255, 255, 1);

            &.link{
                &::before{
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 20px;
                    background-color: #FFF;
                    z-index: 2;
                }

                iframe{
                    margin-top: -60px;
                    height: calc(100% + 60px);
                }
            }

            iframe{
                border: 1px solid #FFF;
            }
        }
    }

    &.show{
        z-index: 1300;
        display: block;
        opacity: 1;
        transition: opacity 0.5s ease !important;

        :global {
            .interactive-wrap-drawer{
                //left: 0;
                opacity: 1;
                //transition: all 1s ease 1s !important;
                transition: all 0.5s !important;
            }

            .interactive-wrap-rightbar{
                //right: 0;
                opacity: 1;
                //transition: opacity 1s ease 2s !important;
                transition: opacity 0.5s ease !important;
            }
        }
    }
}

@media only screen and (max-width: 1560px) and (min-width: 1406px) {
    .interactiveWrap{
        iframe{
            margin-top: -80px !important;
            height: calc(100% + 70px) !important;
        }
    }
}

@media only screen and (max-width: 1405px) and (min-width: 1326px) {
    
    .interactiveWrap{
        iframe{
            margin-top: -100px !important;
            height: calc(100% + 90px) !important;
        }
    }

}

@media only screen and (max-width: 1325px) {
    
    .interactiveWrap{
        iframe{
            margin-top: -126px !important;
            height: calc(100% + 110px) !important;
        }
    }

}