@import '../../../themes/ikiTheme.scss';

.inputChipHolder :global {
    .input-error {
        display: flex;
        align-items: center;
        margin-top: 0 !important;
        margin-bottom: 11px !important;
        font-family: $font-default !important;
        svg {
            width: 19px;
            height: 19px;
            margin-right: 5px;
        }
    }
}

.inputChip :global {
    .MuiInputBase-root {
        background-color: $field-background;
        &:hover:not(.Mui-error) {
            .MuiOutlinedInput-notchedOutline {
                border-color: $field-border-hover;
            }
        }
        &.Mui-focused:not(.Mui-error) {
            .MuiOutlinedInput-notchedOutline {
                border-color: $field-border-focus;
            }
        }
        .MuiInputBase-input {
            background-color: $field-background;
            //font-family: $font-default;
            font-family: "Open Sans", sans-serif;
            font-size: $font-size-input;
            letter-spacing: 0;
            &::placeholder {
                color: $field-placeholder;
                font-size: $font-size-input;
            }
        }
        .MuiOutlinedInput-notchedOutline {
            border-width: $border-width;
            border-style: solid;
            border-color: $field-border-default;
        }
        /*.MuiSelect-selectMenu {
            padding-top: 9.5px !important;
            padding-bottom: 9.5px !important;
        }*/
        &.MuiInputBase-marginDense {
            .MuiSelect-selectMenu {
                padding-top: 9.5px !important;
                padding-bottom: 9.5px !important;
            }
        }
    }
    .MuiInputLabel-root{
        color: $text-color-label;
        //font-family: $font-default;
        font-family: "Open Sans", sans-serif;
        font-size: $font-size-sm;
        transform: translate(14px, -6px) scale(1) !important;
        background-color: #fff;
        padding-right: 4px;
    }
}

.startIcon {
    color: rgba(0, 0, 0, 0.54);
}

.has-startIcon :global {
    .MuiInputBase-input {
        margin-left: 6px;
    }
}

.of-filter-type {
    width: 150px;
}