@import '../../themes/ikiTheme.scss';

//.logoHolder {}

.loginLogo {
  //opacity: 0.5;
  span {
    //color: #3f51b5;
    //font-family: $font-default;
    font-family: "Roboto", "Helvetica", sans-serif !important;
    text-align: center;
    letter-spacing: 1.4px;
  }
  span:nth-child(1) {
    font-weight: 600;
  }
  span:nth-child(2) {
    font-weight: 400;
    opacity: 0.7;
  }

  &.small {
    padding: 30px 13px 13px 20px;
    text-align: left;
    font-size: 14px;
  }
  &.normal {
    padding: 30px 20px 40px 20px;
    text-align: center;
    font-size: 14px;
  }
  &.big {
    padding: 30px 20px 40px 20px;
    text-align: center;
    font-size: 30px;
  }

  &.primary {
    color: #3f51b5;
  }
  &.white {
    color: #ffffff;
  }

  &.minimized {
    padding: 30px 20px 48px 16px;
    font-size: 22px;
  }
}