@import '../../../themes/ikiTheme.scss';
 
 .mainContainer {
    padding-top: 26px;
    padding-left: 32px !important;
    padding-right: 32px !important;
    & * {
        //font-family: $font-default;
        font-family: "Open Sans", sans-serif;
    }

    /*.logoHolder {

    }*/
 }

 /*.logoHolder > * {
    font-family: "Roboto", "Helvetica", sans-serif !important;
 }*/

 .box {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  padding: 40px;
  border: 1px solid rgb(230, 230, 230);
  border-radius: $border-radius;
  background-color: #ffffff;
 }

 .mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  z-index: 9999;
}

.mainTitle {
    color: $text-main-title;
    font-family: $font-default !important;
}

.subtitle {
  text-align: center;
  padding: 13px 0;
  color: $text-main-descr;
  font-family: $font-default !important;
}
  
.subtitleDivider {
  height: 3px !important;
  margin-top: 15px !important;
}

.loginForm {
  width: 100%; /* Fix IE 11 issue.*/
  margin-top: 30px;
  .submitError {
    color: $error;
    font-size: $font-size-sm;
  }
}

.loginCopyright {
  background: transparent;
  box-shadow: 0 0 0 0 transparent;
  border: 0;
  margin: 20px 0;
}

// needs to be added to SignUp page - and renamed to suit styling rules
.license-box{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.license-box span.label-txt{
  float: left;
  color: rgba(0, 0, 0, 0.54);
}
.license-label-box{
  text-align: left;
}

.license-box.has-error .MuiIconButton-root,
.license-box.has-error span{
  color: #f44336 !important;
}

.license-dialog-content{
  color: #555;
  text-align: justify;
}

.ol-level-2{
  padding-left: 13px;
}
.ol-level-2 li{
  margin-top: 6px;
  margin-bottom: 6px;
}