
.txt-dialog-faded1 {
    color: $secondary;
    font-size: $font-size-sm;
    font-weight: $font-light;
} 

.txt-dialog-impact1 {
    color: $text-color;
    font-size: $font-size-base;
    font-weight: $font-bold;
    text-transform: capitalize;
} 

.txt-dialog-impact2 {
    color: $text-color;
    font-size: $font-size-m;
    font-weight: $font-bold;
    &.error {
        color: $error;
    }
} 

.no-results {
    display: inline-flex;
    align-items: center;
    color: $secondary !important;
    font-size: $font-size-base !important;

    .no-results-icon {
        margin-right: 7px;
    }
}

.txt-right {
    text-align: right;
}

.error {
    color: $error;
}

.primary {
    color: $primary;
}

.secondary {
    color: $secondary;
}

.success {
    color: $succes !important;
}

// clip text

.clip-txt{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

// error log string re-structure with css

.error-log-string {
    width: 100%;
    overflow-wrap: break-word;
    white-space: pre-wrap;
}

// error (for those not defined in component)

.input-error {
    display: flex;
    align-items: center;
    margin-top: 0 !important;
    margin-bottom: 11px !important;
    font-family: "Open Sans", sans-serif !important;

    & svg {
        width: 19px;
        height: 19px;
        margin-right: 5px;
    }
}

// Body text override Robot to Open Sans

.MuiTypography-body1, .MuiTypography-body2 {
    font-family: "Open Sans", sans-serif !important;
}