
/* Ikigai Loader styles */

.iki-loader-wrap{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #FBFCFD;
    opacity: 1;
    /*transition: opacity 0.5s ease;*/
}
.iki-loader-wrap.viz{ 
  position: absolute; 
  top: 0;
  left: 0;
  right: 240px;
  //width: 100%;
  height: 100%;
  animation: fadeIn ease 1s;
  -webkit-animation: fadeIn ease 1s;
  -moz-animation: fadeIn ease 1s;
  -o-animation: fadeIn ease 1s;
  -ms-animation: fadeIn ease 1s;
}
.iki-loader-wrap.facet-doc{ 
  position: absolute; 
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
}
.iki-loader-wrap.next-to-nav{ 
  position: absolute;
  left: 250px;
  height: 100%;
  width: calc(100% - 250px);
  overflow: hidden;
}
.iki-loader-wrap.relative{ 
  position: relative;
}
.iki-loader-wrap.hide{
    opacity: 0;
    transition: z-index 0s 0.8s, opacity 0.5s ease 0.3s;
    z-index: -1;
}

.iki-loader-txt {
    margin-top: 20px;
    text-align: center;
    color: #6c757d;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
  }

/* Custom spinner */

.iki-loader-wrap .iki-loader-inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.iki-loader-wrap .iki-loader-inner .iki-spinner {
    position: relative;
    border: 7px solid #e8e8e8;
    border-top: 7px solid #3f51b5;
    border-radius: 60px;
    width: 60px;
    height: 60px;
    animation: spin 1.5s linear infinite;
}

@keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

/* END - Custom spinner */

/* Back button */

.ikiloader-back-btn-holder{
  margin-top: 24px;
}

.ikiloader-back-btn-holder .back-icon{
  -ms-transform: rotate(180deg); /* IE 9 */
  transform: rotate(180deg); /* Standard syntax */
}

@keyframes fadeIn{
  0% { opacity:0; }
  100% { opacity:1; }
}
@-moz-keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}
@-webkit-keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}
@-o-keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}
@-ms-keyframes fadeIn {
  0% { opacity:0; }
  100% { opacity:1; }
}

