.form-container {
  /*overflow: scroll;*/
  overflow: auto;
  /*min-height: 200px;*/
}

.flow-nested-arg {
  //width: calc(100% - 20px);
  width: 100%;

  &.in-pipeline {
    margin-top: 11px;

    .flow-addon-associated_datasets{
      .flow-nested-associated-btn {
        top: 16px;
      }
    }
  }
}
.flow-nested-arg.in-add-dash-dialog {
  width: 100%;
}

.flow-nested-arg-label {
  //   position: absolute;
  //   padding-left: 5px;
  //   padding-right: 5px;
  //   top: -12px;
  //   left: 10px;
  //   background-color: #ffffff;
  flex: 1;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}

.flow-nested-input-group {
  width: calc(100% - 20px);
  /*background-color: #ede7e7;*/
  background-color: transparent;
  /*border: 1px solid rgba(63, 81, 181, 0.45);*/
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  padding: 5px 20px 5px 20px;
  margin-top: 20px;
  margin-bottom: 30px;
  position: relative;

  .flow-nested-delete-btn {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
  }
  
}

.flow-addon-associated_datasets{
  background-color: transparent;
  /*border: 1px solid rgba(63, 81, 181, 0.45);*/
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 20px;
  position: relative;
  width: calc(100% - 20px);
  .flow-nested-associated-btn {
    position: absolute;
    top: 0;
    right: 0;
    //right: 20px;
    z-index: 2;
    transform: translate(50%, -50%);
  }
  .flow-nested-associated-btn.MuiFab-root.Mui-disabled{
    background-color: rgb(230,230,230);
  }
  .MuiOutlinedInput-root{
    //width: 91% !important;
    width: 100%;
  }
}
.flow-nested-arg-title {
  display: flex;
  align-items: center;
  color: #4b5cba;
}

.form-input {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.form-input .tooltip-holder.form-help-icon {
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  top: 8px;
  right: 11px;
  color: #3f51b5 !important;
  /*color: #fff !important;*/
  background-color: #fff;
  border: 1px solid #3f51b5 !important;
  /*border: 1px solid #3f51b5;*/
  border-radius: 100%;
  opacity: 0.85;
}
.form-input .tooltip-holder.form-help-icon:hover {
  opacity: 1;
  cursor: pointer;
}

.help-popover::before {
  content: '';
  position: absolute;
  top: -20px;
  right: 5px;
  border-bottom: 10px solid white;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-top: 10px solid transparent;
  z-index: 10;
}

.dialog-title:not(:first-child) {
  margin-top: 15px;
}

.dialog-title .chain-and-group {
  /*color: #3F51B5;*/
  /*color: #555;*/
  font-size: 12px;
  opacity: 0.5;
  font-weight: 500;
}

.chain-group-title {
  font-size: 12px;
}

.facet-group-title {
  font-size: 12px;
}

.facet-title{
  position: relative;
  display: inline-block;
}

.ace_editor {
  max-height: 450px;
  margin-bottom: 8px;
}

.ace_editor.ace-kuroir {
  /*width: 100% !important;*/
  max-width: 100%;
  border-radius: 5px !important;
}

.ace_tooltip.ace_doc-tooltip{
  z-index: 12001 !important;
}

.dialog-help {
  position: absolute;
  top: 32px;
  right: 32px;
  opacity: 0.85;
  /*border: 1px solid rgba(63, 81, 181, 0.2);
  border-radius: 5px;*/
  padding: 2px 4px;
  display: inline-flex;
  text-align: right;
  width: 50px;
  cursor: pointer;
  .form-help-icon {
    position: relative;
    top: 0;
    right: 0;
    width: 16px;
    height: 16px;
    padding-right: 3px;
    color: #3F51B5;
    font-size: 12px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .form-help-icon::before {
    content: "Help ";
    font-weight: 400;
    position: relative;
    top: 0;
    right: 6px;
  }
}
.dialog-help:hover {
  opacity: 1;
}

.help-dialog-popup {
   .info-example-block {
      //overflow: scroll;
      //height: 90%;
      img {
        width: 100%;
        border: 1px solid rgba(85, 85, 85, 0.14);
      }
    }
    .info-block{
        //max-height: 600px;
        height: calc(100% - 100px);
        overflow-y: auto;
        h1, h2, h3{
            color: #555 !important;
            font-family: "Open Sans", sans-serif;
            font-weight: 600;
        }
        h1{
            font-size: 24px;
        }
        h2{
            font-size: 18px;
        }
        h3{
            font-size: 16px;
        }
        blockquote {
            margin: 0;
            padding: 10px 0 10px 0;
            color: #555;
            ol, ul{
                padding-left: 32px;
                //color: rgba(0, 0, 0, 0.54);
                color: #555;
            }
        }
        p {
            margin-top: 4px;
            //color: rgba(0, 0, 0, 0.54);
            color: #555;
        }
    }
}

.succes-color {
  color: #4caf50 !important;
}

.api-facet-button-group {
  box-shadow: 0 0 0 transparent !important;

  .MuiButton-root {
    text-transform: capitalize !important;
    font-weight: normal !important;
  }
}