@charset "UTF-8";
.error-message {
  display: 'flex';
  align-items: 'center'; }

.icon-variant {
  opacity: 0.9;
  margin-right: 20px; }

.snackbar {
  display: flex; }

.error-snackbar {
  margin: 0 auto !important;
  background-color: #d32f2f !important;
  color: #fff !important; }

.main-container {
  padding-top: 26px; }

.back-to-dashboard {
  display: flex;
  align-items: center;
  height: 100%; }
  .back-to-dashboard .MuiIconButton-root,
  .back-to-dashboard h5 {
    color: rgba(255, 255, 255, 0.8); }

.page-header-title a {
  font-size: 10px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5); }

.page-header-title a:hover {
  color: rgba(0, 0, 0, 0.8); }

.page-header-title .page-header-subtitle {
  font-weight: bold;
  color: #555555;
  font-size: 16px; }
  .page-header-title .page-header-subtitle .project-name {
    margin-right: 10px; }

.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .page-header .page-header-buttons {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .page-header .page-header-buttons .more-button {
      margin-right: 0; }

.cardbox.flow-box,
.cardbox.dataset {
  position: relative;
  padding: 13px 29px; }

.cardbox .cardbox-header {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .cardbox .cardbox-header .flow-text {
    font-size: 12px;
    margin-left: 0;
    margin-bottom: 0; }
    .cardbox .cardbox-header .flow-text .MuiSvgIcon-root {
      font-size: 20px;
      margin-top: -2px;
      color: #b2b0b0;
      height: 15px;
      width: auto; }
    .cardbox .cardbox-header .flow-text span {
      margin-left: 8px;
      color: #b2b0b0;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      font-family: 'Open Sans', sans-serif;
      font-size: 10px !important;
      font-weight: normal; }
  .cardbox .cardbox-header .flow-menu {
    float: right;
    margin-right: -16px; }

.cardbox .content {
  /*padding-left: 0;
      padding-right: 0;
      cursor: pointer;*/ }
  .cardbox .content h6 {
    color: #3f51b5 !important;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px !important;
    font-weight: bold !important;
    letter-spacing: 0 !important;
    text-transform: capitalize; }
  .cardbox .content .MuiTypography-caption {
    font-family: 'Open Sans', sans-serif;
    font-size: 12px !important;
    font-weight: normal !important;
    color: #b2b0b0 !important;
    letter-spacing: 0 !important;
    font-style: normal !important; }

.cardbox.dataset .content h6 {
  text-transform: none; }

.flow-tab-container {
  margin-top: 26px; }

.flows-control-wrap {
  position: relative;
  overflow: hidden; }

.flows-filter-holder {
  margin-top: 12px !important;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  width: 60%;
  height: 40px;
  justify-content: flex-end; }

.flows-filter-holder .project-filters-textfield.MuiFormControl-marginNormal {
  margin-top: 0;
  margin-bottom: 0; }

.flows-filter-box {
  width: 100%;
  height: 40px;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: -100%;
  display: flex;
  justify-content: flex-end;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  z-index: 1;
  background-color: #fbfcfd; }

.flows-filter-box.show {
  right: 40px;
  opacity: 1;
  transition: all 0.3s ease-in-out; }

.flows-pagination {
  display: inline-flex;
  overflow: hidden !important;
  height: 36px;
  align-items: center; }

.filter-toggle-button {
  height: 100%;
  background-color: #fbfcfd;
  display: inline-block;
  position: relative;
  z-index: 2; }

.filter-toggle-button.selected button {
  background-color: #ebebeb; }

.cardbox.dataset.uploading {
  padding-bottom: 20px; }

.uploading-databox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(235, 235, 235, 0.4);
  z-index: 99;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 0 24px;
  padding-bottom: 8px;
  /*.linear-progress{
      width: 100%;
    }*/ }
  .uploading-databox .linear-progress-val {
    font-size: 12px; }
