/* Tree view */

.tree-view-holder-title {
  margin: 30px 5px 0 24px;
  font-size: 22px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.2) !important;
  opacity: 0;
}

.tree-view-holder {
  margin: 3px 5px 0 24px;
  //color: rgba(63, 81, 181, 1);
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: normal;
  max-height: calc(100% - 124px);
  overflow-y: auto;
  scrollbar-width: thin; /* Firefox scrollbar */
  scrollbar-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.03); /* Firefox scrollbar */

  &::-webkit-scrollbar {
    width: 7px;
    height: 7px;
  }
  &::-webkit-scrollbar-track-piece  {
    background-color: rgba(0, 0, 0, 0.03);
  }
  &::-webkit-scrollbar-thumb:vertical {
    height: 30px;
    background-color: rgba(0, 0, 0, 0.1);
  }

  .tree-view-component{
    margin-right: 5px;

    .MuiTreeItem-root {
      &.grabbable {
        .MuiTreeItem-content {
          cursor: move !important; /* fallback if grab cursor is unsupported */
          cursor: grab !important;
          cursor: -moz-grab !important;
          cursor: -webkit-grab !important;
          border-radius: 4px;

          /* (Optional) Apply a "closed-hand" cursor during drag operation. */
          &:active { 
            cursor: grabbing !important;
            cursor: -moz-grabbing !important;
            cursor: -webkit-grabbing !important;
            //background-color: rgba(63, 81, 181, 0.2);
            //color: #fff;
            border-radius: 4px;
          }
        }
      }

      &.Mui-selected {
        background-color: rgba(63, 81, 181, 0.02);
        border-radius: 4px;

        & > .MuiTreeItem-content {
          background-color: rgba(63, 81, 181, 0.04);

          .MuiTreeItem-label {
            background-color: transparent !important;
          }
        }
      }

      .MuiTreeItem-content:hover {
        background-color: rgba(63, 81, 181, 0.02);

        .MuiTreeItem-label {
          background-color: transparent !important;
        }
      }

      .MuiTreeItem-content {
        //opacity: 0.75;
        opacity: 0.95;
        height: 38px;
        margin-bottom: 7px;
        display: flex;
        align-items: center;
        width: 100%;
        border-radius: 5px !important;
        position: relative;
        margin-left: 0;
        padding-left: 3px;
        color: rgba(255, 255, 255, 0.9);

        &:hover {
          color: rgba(255, 255, 255, 1);
        }

        .drag-icon {
          margin-right: 8px;
        }

        .MuiTypography-body1,
        .MuiTypography-body1 span:not(.form-help-icon) {
          font-family: 'Open Sans', sans-serif !important;
          font-size: 16px !important;
          font-weight: normal !important;
          white-space: nowrap;
        }

        .MuiTreeItem-label {
          font-size: 16px !important;
          height: 38px;
          display: flex;
          align-items: center;
          overflow: hidden;
        }
      }

      .MuiTreeItem-iconContainer {
        width: auto;
        //margin-right: 0; 
      }
    }
  }
}

/******
TOOLTIP 
*******/

.form-help-icon {
  /*background-color: rgba(255, 255, 255, 0.85);*/
  border: 1px solid rgba(255, 255, 255, 0.85);
  border-radius: 20px;
  margin-left: auto;
  margin-right: 7px;
  position: absolute;
  top: 11px;
  right: 0;
  width: 16px;
  height: 16px;
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.drawer-help.form-help-icon {
  //background-color: rgba(255, 255, 255, 0.24);
  background-color: #5c6bc0;
  border: 1px solid rgba(255, 255, 255, 0.35);
  border-radius: 20px;
  margin-left: auto;
  margin-right: 7px;
  position: absolute;
  top: 11px;
  right: 0;
  width: 16px;
  height: 16px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 13px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.tooltip {
  /*background-color: yellow;*/
}

.default-popper .MuiTooltip-tooltip {
  background: rgba(0, 0, 0, 0.54) !important;
  border: none !important;
  padding: 4px 8px !important;
  color: #FFF !important;
  font-size: 12px !important;
  box-shadow: none !important;
}

.default-popper .MuiTooltip-tooltip .MuiTooltip-arrow{
  color: rgba(0, 0, 0, 0.54) !important;
  border-color: rgba(0, 0, 0, 0.54) !important;
  font-size: 4px;
  text-shadow: 0 0 0 0 transparent !important;
  box-shadow: 0 0 0 0 transparent !important;
}

.MuiTooltip-tooltip {
  /*position: relative;*/
  background: rgba(255, 255, 255, 1) !important;
  /*background-color: rgba(255, 255, 255, 1) !important;*/
  /*background-color: #fbfcfd !important;*/
  /*border: 1px solid rgba(63, 81, 181, 0.2) !important;*/
  border: 1px solid rgba(0, 0, 0, 0.04) !important;
  padding: 26px !important;
  color: rgba(0, 0, 0, 0.54) !important;
  font-family: 'Open Sans', sans-serif !important;
  font-size: 14px !important;
  box-shadow: 0 0 10px -5px rgba(0, 0, 0, 0.33) !important;
  /*min-width: 450px;*/
  /*height: 500px;*/
  .MuiTooltip-arrow {
    color: #fbfcfd !important;
    border-color: #fbfcfd;
    font-size: 10px;
    text-shadow: -4px -4px 4px -5px rgba(0, 0, 0, 0.75) !important;
    box-shadow: -4px -4px 4px -5px rgba(0, 0, 0, 0.33) !important;
  }
  .example-block {
    overflow: scroll;
    height: 90%;
  }
  .info-block{
    max-height: 600px;
    overflow-y: auto;
    h1,h2,h3{
      color: #555 !important;
      font-family: "Open Sans", sans-serif;
      font-weight: 600;
    }
    h1{
      font-size: 24px;
    }
    h2{
      font-size: 18px;
    }
    h3{
      font-size: 16px;
    }
    blockquote{
      padding: 10px 0 10px 0;
      color: #555;
      ol, ul{
        padding-left: 32px;
        color: rgba(0, 0, 0, 0.54);
      }
    }
    p{
      margin-top: 4px;
      color: rgba(0, 0, 0, 0.54);
    }
  }
  .help-tabs {
    margin-bottom: 24px;
    color: #3f51b5 !important;
    .Mui-selected .MuiTouchRipple-root{
      border-bottom: 3px solid;
    }
    button {
      min-width: auto;
      font-weight: bold;
    }
    border-bottom: 1px solid rgba(85, 85, 85, 0.14);
  }
}
