  body {
    margin: 0;
    padding: 0;
    color: $text-color !important;
    font-size: $font-size-base !important;
    /*font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
      Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol',
      'Noto Color Emoji' !important;*/
    font-family: "Open Sans", sans-serif !important;
    font-variant: tabular-nums !important;
    background-color: $body-background !important;
    font-feature-settings: 'tnum' !important;
    line-height: normal;
    //line-height: 1.43;
    overflow: auto !important;
  }

  *, ::after, ::before {
    box-sizing: border-box;
  }

  a {
    cursor: pointer;
    text-decoration: none !important;
    outline: 0 !important;
  }

  svg:not(:root) {
    overflow: initial !important;
  }

  /****************************
MOBILE-SMALL DEVICES FEEDBACK 
*****************************/

.mobile-wrap-container {
  width: 100%;
  height: 100%;
  background: #ffffff;
  position: absolute;
  z-index: 9999999;
}

.mobile-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.mobile-wrap .logo {
  width: 100%;
  padding: 20px 13px 40px 13px;
  color: #3f51b5;
  font-family: 'Open Sans', sans-serif;
  font-size: 30px;
  text-align: center;
  letter-spacing: 1.2px;
}

.mobile-wrap .logo span.ikigai {
  font-weight: bold;
}

.mobile-wrap .logo span.labs {
  font-weight: normal;
  opacity: 0.7;
}

.icon-holder-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  width: 130px;
  margin: 0 auto;
}

.mobile-wrap i.zmdi {
  font-size: 40px;
  opacity: 0.75;
}

.mobile-wrap i.zmdi.small {
  font-size: 20px;
  opacity: 0.4;
}

.mobile-wrap .txt-holder {
  margin-top: 20px;
  color: #555555;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
}

.mobile-wrap .copyright {
  margin-top: 20px;
  color: #777777;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
}