// This is temporarily style file - in future all style definitions should be transfered under components directly

// radio group
.radio-btn-holder.with-label {
    position: relative;
    padding: 16px;
    border: 2px solid rgba(0,0,0,.09);
    border-radius: 4px;
    width: 100%;

    &.horizontal {
      padding: 0;
      margin-top: 0;
      border: 0;

      .MuiFormGroup-root {
        display: block;
      }
    }

    .MuiFormLabel-root {
        position: absolute;
        top: -8px;
        left: 8px;
        padding-left: 5px;
        font-size: 12px;
        background-color: #fff;

        &.Mui-focused {
            color: rgba(0, 0, 0, 0.54);
        }
    }

    .MuiFormControlLabel-root {
        .MuiFormControlLabel-label {
            font-size: 14px !important;
            font-weight: 400 !important;
        }
    }
}

// Date - Time pickers

.time-controls-holder {
  .time-controls {
    display: flex;
    justify-content: space-between;
    margin-top: 11px;

    .time-control {
      width: 100%;

      .MuiFormControl-root {
        margin-top: 16px;
      }
    }

    .MuiFormControl-root {
      margin-right: 7px;

      .MuiOutlinedInput-notchedOutline {
        border: 2px solid rgba(0, 0, 0, 0.09);

        &:nth-child(1) {
          margin-right: 7px;
        }
      }

      &:hover {
        .MuiOutlinedInput-notchedOutline {
          border-color: rgba(0, 0, 0, 0.23);
        }
      }

      .Mui-focused {
        .MuiOutlinedInput-notchedOutline {
          border-color: rgba(63, 81, 181, 1);
        }
      }
    }
  }
}

// table pagination
.MuiTablePagination-root {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &.pagination-sm {
        margin-top: 16px;
        
        .MuiTablePagination-toolbar > *:not(.MuiTablePagination-actions){
            display: none;
        }
    }
}

// help tooltip icon - questionmark
.tooltip-icon {
    background-color: #5c6bc0;
    border: 1px solid rgba(255, 255, 255, 0.35);
    border-radius: 20px;
    width: 16px;
    height: 16px;
    color: rgba(255, 255, 255, 0.8);
    font-size: 13px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
}

.drawer-help.form-help-icon {
    background-color: #5c6bc0;
    border: 1px solid rgba(255, 255, 255, 0.35);
    border-radius: 20px;
    margin-left: auto;
    margin-right: 7px;
    position: absolute;
    top: 11px;
    right: 0;
    width: 16px;
    height: 16px;
    color: rgba(255, 255, 255, 0.8);
    font-size: 13px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
}

