/******** 
ANIMATION
*********/

/* Fade in */

.fade-in {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
  }
  @keyframes fadeIn{
    0% { opacity:0; }
    100% { opacity:1; }
  }
  @-moz-keyframes fadeIn {
    0% { opacity:0; }
    100% { opacity:1; }
  }
  @-webkit-keyframes fadeIn {
    0% { opacity:0; }
    100% { opacity:1; }
  }
  @-o-keyframes fadeIn {
    0% { opacity:0; }
    100% { opacity:1; }
  }
  @-ms-keyframes fadeIn {
    0% { opacity:0; }
    100% { opacity:1; }
  }
  
  /* Fade in */
  
  .fade-in-out {
    animation: fadeInOut ease 1s;
    -webkit-animation: fadeInOut ease 1s;
    -moz-animation: fadeInOut ease 1s;
    -o-animation: fadeInOut ease 1s;
    -ms-animation: fadeInOut ease 1s;
  }
  @keyframes fadeInOut{
    0% { opacity:0; }
    50% { opacity:1; }
    80% { opacity:1; }
    100% { opacity:0; }
  }
  @-moz-keyframes fadeInOut {
    0% { opacity:0; }
    50% { opacity:1; }
    80% { opacity:1; }
    100% { opacity:0; }
  }
  @-webkit-keyframes fadeInOut {
    0% { opacity:0; }
    50% { opacity:1; }
    80% { opacity:1; }
    100% { opacity:0; }
  }
  @-o-keyframes fadeInOut {
    0% { opacity:0; }
    50% { opacity:1; }
    80% { opacity:1; }
    100% { opacity:0; }
  }
  @-ms-keyframes fadeInOut {
    0% { opacity:0; }
    50% { opacity:1; }
    80% { opacity:1; }
    100% { opacity:0; }
  }
  
