  }

  // Checkbox

  .checkbox-form-control {
    padding: 8px 4px !important;
    
    .MuiFormControlLabel-label{
      font-family: 'Open Sans', sans-serif;
      font-size: 14px;
      font-weight: normal !important;
      color: #555;
    }
  }

  // Stepper

  .newSchedule {
    &.MuiStepper-root {
        padding: 0 !important;

        .MuiStepLabel-labelContainer {
          span {
            font-family: "Open Sans", sans-serif !important;
          }
        }

        .subject-cardbox-wrap {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-gap: 10px 10px;

          .subject-cardbox {
            padding: 16px;
            border: 2px solid rgba(63, 81, 181, 0.16);
            border-radius: 4px;
            text-align: center;

            .subject-cardbox-icon {
              svg {
                font-size: 26px;
                //font-weight: bold;
                color: #BEBEBE;
              }
            }

            .subject-cardbox-title {
              font-size: 16px;
              font-weight: 600;
              color: rgba(63 ,81, 181, 0.54);
            }

            :hover {
              cursor: pointer;
            }

            &.active {
              border: 2px solid rgba(63, 81, 181, 1);

              .subject-cardbox-icon {
                svg {
                  color : #A8A7A7;
                }
              }

              .subject-cardbox-title {
                color: rgba(63 ,81, 181, 1);
              }
            }

            &.disabled {
              cursor: no-drop;

              & * {
                cursor: no-drop;
              }
            }
          }
        }

        .MuiStepContent-root {
          padding-top: 9px;  
          margin-top: 13px;
        }
    }
  }

  // Mui Picker Dialog

  .MuiPickersModal-dialogRoot {
    .MuiPickersModal-dialog {
      padding-top: 32px !important;

      .MuiPickersClockNumber-clockNumber {
        font-weight: 700;
      }
    }

    .MuiDialogActions-root {
      padding-bottom: 36px !important;

      .MuiButtonBase-root {
        .MuiButton-label {
          color: #555;
          text-transform: capitalize !important;
        }
      }
    }
  }

  // Expand panel

  .expand-panel {
    box-shadow: 0 0 0 transparent !important;
    border: 1px solid rgba(0, 0, 0, 0.09) !important;
  }

  // when disabled - cursor

  .disabled {
    cursor: no-drop !important;
  }

  /* Add Dash Dialog */

  .flow-nested-arg {
    &.in-add-dash-dialog {
      .flow-addon-associated_datasets{ 
        margin-top: 10px;

        .flow-nested-associated-btn {
          top: 10px;
        }
      }
    }
  }
  
