.dashCentralPanel{
    position: absolute;
    top: -88px;
    left: 60px;
    right: 240px;
    //height: calc(100% + 88px);
    height: calc(100% + 80px);
    border: 0;
    z-index: 1;

    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 110px;
        background-color: #FFF;
        z-index: 2;
    }

    .metabaseIframe{
        width: 100%;
        height: 100%;
        border: 0;
    }
}