// colors
$primary: #3f51b5;
$secondary: #b2b0b0;
$body-background: #fbfcfd;
$navigation-background: $primary;
// fonts
$text-color: #555555;
$text-color-primary: $primary;
$text-color-secondary: $secondary;
$text-color-label: rgba(0, 0, 0, 0.54);
$text-color-navigation: #fff;
$text-color-control: #fff;
$text-main-title: #252525;
$text-main-descr: #6c757d;
$font-path: '/fonts';
$font-default: 'Open Sans';
$font-size-xs: 11px;
$font-size-sm: 12px;
$font-size-base: 14px;
$font-size-input: 14px;
$font-size-m: 18px;
$font-size-l: 20px;
$font-size-xl: 22px;
$font-size-xxl: 24px;
$font-light: 400;
$font-semibold: 500;
$font-bold: 700;
$letter-spacing-btn: 0.56px;
$line-height-m: 21px;
//borders
$border-normal: rgba(0, 0, 0, 0.09);
$border-primary-light: rgba(63, 81, 181, 0.1);
$border-primary-light-hover: rgba(63, 81, 181, 0.25);
$border-radius: 5px;
$border-width: 2px;
$border-active: $primary;
// buttons
$btn-deafult-bg: transparent;
$btn-deafult-color: #444444;
$btn-deafult-bg-hover: rgba(68, 68, 68, 0.06);
$btn-deafult-bg-focus: rgba(68, 68, 68, 0.12);
$btn-deafult-bg-active: rgba(68, 68, 68, 0.18);
$btn-primary-bg-hover: #555555;
$btn-primary-bg-focus: #333333;
$btn-primary-bg-active: #222222;
// fields
$field-color: #aaaaaa;
$field-background: #ffffff;
$field-placeholder: rgba(0, 0, 0, 0.54);
$field-border-default: rgba(0, 0, 0, 0.09);
$field-border-hover: rgba(0, 0, 0, 0.23);
$field-border-focus: $primary;
$field-border-disabled: #cccccc;
$field-bg-diabled: #eeeeee;
// select 
$select-item-bg: transparent;
$select-item-bg-selected: rgba(68, 68, 68, 0.18);
$select-item-bg-active: rgba(68, 68, 68, 0.12);
$select-item-bg-hover: rgba(68, 68, 68, 0.12);
$select-item-remove-icon: #ededed;
// chips - tags / button-picker
$chip-default: rgba(68, 68, 68, 0.06);
$chip-hover: rgba(68, 68, 68, 0.12);
$chip-selected: rgba(68, 68, 68, 0.18);
$chip-active: rgba(68, 68, 68, 0.24);
// succes
$succes: #4caf50;
// info
$info: #2196f3;
// warning
$warning: #ffc107;
// errors
$error: #f44336;
// breadcrumb
$breadcrumb-link: rgba(0, 0, 0, 0.54);
$breadcrumb-link-size: 11px;
$breadcrumb-link-weight: 400;
$breadcrumb-title: #555555;
$breadcrumb-title-size: 20px;
$breadcrumb-title-weight: 700;
// breakpoints
$mobile: 736px;
$mobile-xs: 415px;