/* ALERT styles */

.alert{
    padding: 16px !important;

    .title{
        color: #555;
        text-transform: capitalize;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 16px;
    }

    .descr{
        margin: 2px 0;
        color: #555;

        div{
            margin-bottom: 8px;
        }
    }

    .buttons{
        margin: 8px 0 6px 0;

        :global(button) {
            margin-right: 7px;
        }
    }

    &.MuiAlert-standardWarning {
        .MuiAlert-message {
            .buttons{
                margin: 8px 0 6px 0;
        
                :global(button) {
                    margin-right: 7px;
                }
            }
        }
    }
}