
.cardbox-holder {
    width: 100%;
    margin: 16px 0 32px 0;
    padding: 32px 32px 40px 32px;
    background-color: #ffffff;
    //border: 2px solid rgba(0, 0, 0, 0.04);
    //box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 2px -1px rgba(0, 0, 0, 0.8) !important;
    border-radius: 5px;
    font-family: 'Open Sans', sans-serif;

    &.contains-calendar {
        height: 100%;
    }

    .filters-and-pagination {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 32px;

        .table-filters {
            & > div {
                margin-right: 10px;
            }

            /*.MuiButtonGroup-root {
                margin-top: -1px;
            }*/
        }

        .table-pagination {
            display: flex;
            align-items: center;
        }
    }

    .remove-pt {
        padding-top: 0 !important;
    }

    .cardbox:not(.has-table):not(.page-content) {
        position: relative;
        padding: 13px 29px;
        border: 3px solid rgba(63, 81, 181, 0.10);
        border-radius: 5px;
        background-color: #ffffff;
        box-shadow: 0 0 0 transparent !important;

        &:hover {
            border: 3px solid rgba(63, 81, 181, 0.25);
        }
        &.uploading:hover {
            border: 3px solid rgba(63, 81, 181, 0.10);
        }
        &.clicked {
            border: 3px solid rgba(63, 81, 181, 0.4) !important;
        }

        .cardbox-header {
            p {
                display: flex;
                align-items: center;
                font-size: 12px;
                margin-left: 0;
                margin-bottom: 0;

                .MuiSvgIcon-root {
                    font-size: 20px;
                    margin-top: -2px;
                    color: #b2b0b0;
                    height: 15px;
                    width: auto;
                }
                span {
                    margin-left: 8px;
                    color: #b2b0b0;
                    text-transform: uppercase;
                    letter-spacing: .1em;
                    font-family: Open Sans,sans-serif;
                    font-size: 10px !important;
                    font-weight: 400;
                }
            }
            .MuiIconButton-root {
                padding: 6px !important;
            }
        }
        .share-btn {
            text-transform: capitalize !important;
        }
        .title {
            color: #555555;
            font-family: 'Open Sans', sans-serif;
            font-size: 16px !important;
            font-weight: bold;
            line-height: 1.33;
            letter-spacing: 0em;
        }
        .description {
            color: #555555;
            font-family: 'Open Sans', sans-serif;
            font-size: 14px !important;
            font-weight: normal;
            line-height: 1.33;
            letter-spacing: 0em;
        }
        .content{
            padding-left: 0;
            padding-right: 0;
            cursor: pointer;

            &.table-inside {
                padding: 0;
            }
            .data-row {
                font-family: 'Open Sans', sans-serif;
                margin-bottom: 11px;

                .name {
                    color: #3f51b5 !important;
                    font-family: 'Open Sans', sans-serif;
                    font-size: 16px !important;
                    font-weight: bold !important;
                    letter-spacing: 0 !important;
                    text-transform: capitalize;
                }
                .name-id {
                    font-family: 'Open Sans', sans-serif;
                    font-size: 12px !important;
                    font-weight: normal !important;
                    color: #b2b0b0 !important;
                    letter-spacing: 0 !important;
                    font-style: normal !important;
                }
                .descr {
                    color: #555555;
                    font-family: 'Open Sans', sans-serif;
                    font-size: 14px;
                    font-weight: normal !important;
                }

                .data-row-upper {
                    color: #b2b0b0;
                    font-size: 12px;
                    font-weight: normal;
                }
                .data-row-lower {
                    color: #555555;
                    font-size: 12px;
                    font-weight: normal;
                }
            }
        }
        .footer {
            padding: 16px;
        }
        .icon-button {
            width: 42px;
            height: 42px;
            border-radius: 100%;
            padding: 0;

            &:not(:first-child) {
                margin-left: 11px;
            }
            &.primary {
                border: 1px solid #3f51b5;

                &.filled {
                    color: #fff;
                    background-color: #3f51b5;
                }
                &.outlined {
                    color: #3f51b5;
                    background-color: transparent;
                }
            }
            &.secondary {
                &.filled {
                    background-color: rgba(0, 0, 0, 0.04);

                    &:hover {
                        background-color: rgba(0, 0, 0, 0.11);
                    }
                }
            }
        }
    }

    .cardbox {
        &.page-content {
            height: 100%;
            position: relative;
            padding: 13px 29px;
            border: 1px solid rgba(0, 0, 0, 0.09);
            border-radius: 5px;
            background-color: #ffffff;
            box-shadow: 0 0 0 transparent !important;

            .header {
                margin-bottom: 16px;
                .title {
                    color: #555555;
                    font-size: 20px;
                    font-weight: 700;
                }
            }

            .content {
                .avatar {
                    font-size: 80px;
                }
            }
        }
    }
}

.under-tab-buttons {
    margin: 16px 0 32px 0;

    & > button {
        margin-right: 10px;
    }
}

.filter-cardbox-holder {
    width: 100%;
    margin: 16px 0 32px 0;
    padding: 0;
    border-radius: 5px;
    font-family: 'Open Sans', sans-serif;
}

// filter sidebar and content

.with-filter-wrapper {
    width: 100%;
    display: flex;

    .filter-holder {
        width: 250px;
    }

    .next-to-filter-content {
        width: calc(100% - 250px);
    }
}