@import '../../../themes/ikiTheme.scss';

.breadcrumb {
    .path {
        line-height: 14px;
        margin-bottom: 4px;
        a {
            color: $text-color-label;
            font-size: $font-size-xs;

            i {
                margin-right: 7px;
            }
        }
    }
    .title {
        position: relative;
        color: $text-color;
        //font-family: $font-default;
        font-family: "Open Sans", sans-serif;
        font-size: 20px;
        font-weight: 700;
    }
}