/* Snackbar styles */

.snackbar {
    border-radius: 5px;
    /*box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2),0px 6px 10px 0px rgba(0,0,0,0.14),0px 1px 18px 0px rgba(0,0,0,0.12);*/
    box-shadow: 0px 4px 10px -3px rgba(0, 0, 0, 0.25) !important;
    display: flex;
    align-items: center;
    align-content: center;
  }

  .snackbar > div {
    color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    display: flex;
    align-items: center;
    align-content: center;
  }

  .snackbar .msg {
    
  }

  .snackbar .val {
    font-weight: 600;
  }
  
  .snackbar .MuiSnackbarContent-message {
    color: #ffffff;
    display: flex;
    align-items: center;
    align-content: center;
  }

  .snackbar svg {
    color: #ffffff;
    width: 24px;
    height: 24px;
  }
  
  .snackbar .state-icon {
    margin-right: 16px;
  }

  /* Success */

  .snackbar.success,
  .snackbar.success > div {
    background-color: #4caf50;
  }

  /* Error */

  .snackbar.error,
  .snackbar.error > div {
    background-color: #f44336;
  }

  /* Warning */

  .snackbar.warning,
  .snackbar.warning > div {
    background-color: #ff9800;
  }

  /* Info */

  .snackbar.info, .snackbar.processing,
  .snackbar.info > div, .snackbar.processing > div {
    background-color: #2196f3;
  }