.link-button-group-container {
  overflow: visible;
}

.link-button-group{
  width: 45px;
  margin-top: -100px;
  margin-left: 30px;
}
.link-button-group.show {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s ease !important;
}
.link-button-group.hide {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0.1s ease 0.3s !important;
}
.link-button-group button{
  margin-bottom: 6px;
}

.link-path {
  pointer-events: all;
  transition: stroke 1s ease !important;
  stroke: rgba(63, 81, 181, 1);
}
.link-arrow {
  transition: fill 1s ease !important;
  fill: rgba(63, 81, 181, 1);
}
.link-path-hitarea {
  pointer-events: all;
}
.interactive {
  .link-path {
    stroke: #c7c7c7;
    // pointer-events: none;
    pointer-events: auto;
    cursor: pointer;
  }
  .link-arrow {
    fill: #c7c7c7;
    pointer-events: auto;
    cursor: pointer;
  }
  .link-path-hitarea {
    // pointer-events: none;
    pointer-events: auto;
    cursor: pointer;
  }
}