
.contentArea {
    margin-left: 250px;
    padding-top: 0;
    transition: margin-left 0.2s ease;
    height: 100vh;
    z-index: -1;

    &.minimized {
        margin-left: 60px;
        transition: margin-left 0.2s ease 0.2s;

        :global(.iki-loader-wrap.next-to-nav) {
            left: 60px;
            width: calc(100% - 60px);
        }
    }

    :global(.h_100-100) {
        height: calc(100% - 100px);
    }

    :global(.h-100) {
        height: 100%;
    }

    :global(.main-container) {
        padding: 26px 32px !important;
    }

    :global(.central-panel) {
        position: absolute;
        top: -88px;
        left: 250px;
        right: 240px;
        height: calc(100% + 88px);
        border: 0;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            width: 100%;
            //height: 135px;
            height: 80px;
            background-color: #FFF;
            z-index: 2;
        }

        &.dataset {
            top: -58px;
            height: calc(100% + 58px);
        }
    }

    :global(.metabase-iframe){
        width: 100%;
        height: 100%;
        border: 0;
    }
}