/* 
    Character counter 
*/

.cCounter-holder{
    position: absolute;
    top: 7px;
    right: 8px;
    background-color: #FFF;
    border: 1px solid rgba(0, 0, 0, 0.13);
    border-radius: 4px;
    padding: 0 8px;
    text-align: center;
    color: rgba(0, 0, 0, 0.53);
    font-size: 12px;
}

    .cCounter-holder .left{}

    .cCounter-holder .middle{}

    .cCounter-holder .right{}

/* when Error .. */

.cCounter-holder.has-error{
    color: #f44336;
    border: 1px solid #f44336;
}

/* when Focus .. */

.MuiFormControl-root.has-counter:focus-within .cCounter-holder:not(.cCounter-holder.has-error){
    border: 1px solid #3f51b5;
    color: #555;
}