.dropzone-container {
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  margin-top: 20px;
}

.dropzone-container p {
  font-size: 1rem;
  margin-bottom: 0;
}

.dropzone-container > em {
  font-size: 0.8rem;
}

.dropzone {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border 0.24s ease-in-out;
}

.dropzone:focus {
  border-color: #2196f3;
}

.dropzone.disabled {
  opacity: 0.6;
}

.dropzone-container {
  .upload-file {
    display: flex;
    align-items: center;
    border: 2px solid;
    border-radius: 5px;
    padding-left: 5px;
    justify-content: space-between;
  }
  .upload-file-title {
    display: flex;
    align-items: center;
    .upload-name {
      max-width: 200px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
