/* Dataset somponent styles */

.centralPanel{
    position: absolute;
    //top: -80px;
    top: -58px;
    left: 60px;
    right: 240px;
    //height: calc(100% + 74px);
    height: calc(100% + 50px);
    border: 0;
    z-index: 1;

    &::before{
        content: "";
        position: absolute;
        width: 100%;
        height: 80px;
        background-color: #FFF;
        z-index: 2;
    }

    .metabaseIframe{
        width: 100%;
        height: 100%;
        border: 0;
    }
}

.gobackPopupContainer{
    padding: 24px;

    :global(.goback-popup-title){
        font-size: 16px;
        font-weight: 700;
        flex: 1 1;
        color: #555555 !important;
    }

    :global(.goback-popup-content-txt){
        margin: 16px 0;
        color: #555555 !important;
    }
    :global(.goback-popup-buttons){
        display: flex;
        align-items: center;
        justify-content: flex-end;

        button:not(:last-child) {
            margin-right: 7px;
        }
    }
}

@media only screen and (max-width: 1360px) and (min-width: 1226px) {
    .centralPanel{
        top: -84px;
        height: calc(100% + 75px);

        &::before{
            height: 105px;
        }
    }
}

@media only screen and (max-width: 1225px) and (min-width: 1131px) {
    .centralPanel{
        top: -115px;
        height: calc(100% + 83px);

        &::before{
            height: 105px;
        }
    }
}

@media only screen and (max-width: 1130px) {
    .centralPanel{
        top: -116px;
        height: calc(100% + 105px);

        &::before{
            height: 140px;
        }
    }
}