.MuiMenu-paper {
    box-shadow: 0 4px 10px -3px rgba(0,0,0,.25) !important;

    .MuiMenu-list {
        .MuiListItem-root {
            color: rgba(0,0,0,.54);
            font-family: Open Sans,sans-serif;
            font-size: 14px;
            font-weight: 400;
            background-color: transparent;
            margin-bottom: 6px;
            line-height: 1.33;
            letter-spacing: 0;
            padding: 4px 18px;

            &:hover {
                background-color: transparent !important;
                color: rgba(0,0,0,.76);
            }

            svg {
                width: 18px;
                height: 18px;
                margin-right: 8px;
            }

            &.Mui-selected {
                color: #3f51b5;
            }
        }
    }
}